<script>
import { clientHeight } from '@/api/clientHeight';
export default {
  components: {
  },
  data() {
    return {
      isAnimationgTile: false,
      initValue: "display:block",
      choiceIndex: 0,
      isShow1:false,
      isShow2:false,
      isShow3:false,

    };
  },
  mounted() {
    this.$bus.$on("scrollTopEvent", this.teamMove);
  },
  destroyed() {
    this.$bus.$off("scrollTopEvent", this.teamMove);
  },

  methods: {
    teamMove(){
      let box1top = this.$refs.box1.getBoundingClientRect().top;
      let glap =box1top -  clientHeight
      if(glap<0 && !this.isShow1){
        this.isShow1 = true
      }

      let box2top = this.$refs.box2.getBoundingClientRect().top;
      let glap2 =box2top -  clientHeight
      if(glap2<0 && !this.isShow2){
        this.isShow2 = true
      }

      let box3top = this.$refs.box3.getBoundingClientRect().top;
      let glap3 =box3top -  clientHeight
      if(glap3<0 && !this.isShow3){
        this.isShow3 = true
      }
    }
  },
};
</script>

